import _ from 'lodash'
import '../assets/animate-css/animate.css'
import Vue from 'vue'
import Router from 'vue-router'
import Leader from '../components/leaders.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/leaders',
      component: Leader 
    }
  ]
})



