<template>
<div class="container">
    <div class="row">
        <h3 class="font-heavy">Leaders</h3>
    </div>
    <div class="row">
        <div class="col-12">
            <blockquote>
                Là những thẻ bài màu <span class='green'>xanh lục</span>. 
                Chúng tượng trưng cho những lãnh đạo. Mỗi thẻ sẽ mang lại cho người chơi những khả năng đặc biệt
            </blockquote>
        </div>
    </div>
    <br>
    <br>
    <div class="row">
        <input v-model.trim='search_name' type="text" placeholder="Tìm kiếm lãnh đạo">
    </div>
    <div class="row">
        <transition-group enter-active-class="animated" enter-to-class="fadeInLeft" leave-active-class='animated' leave-to-class="fadeOutLeft" tag='ul' name='leaders'>
            <li class='leader-item' v-for="l,index in leaders_list" v-bind:key="l.name">
                <span class='leader-name font-heavy'>{{l.name}}</span>: {{l.ability}} 
            </li>
        </transition-group>
    </div>
</div>
</template>
<style lang="css" scoped>

.leaders-move{
    transition: all 1s;
}

span.green{
    color: #2ecc71;
}
span.leader-name{
    color: #2ecc71;
}

blockquote{
    border-style:solid;
    margin: 0 0 0 0;
    border-width: 0 0 0 0;
    border-left-width: 3px;
    padding-left: 9px;
    border-left-color: #2c3e50;
}
input{
    padding-left:4px;
    width: 78%;
    border-radius: 4px;
    border-color: black;
    border-style: solid;
    border-width: 2px 2px 2px 2px;
    transition: border-color .4s;
}
input:focus{
    border-color: #2980b9;
}

li{
    margin-top: 5px;
    margin-bottom: 5px;
}
</style>
<script>
    var leaders = require('../assets/json_data/leader.json')['leaders']
    export default{
        
        data: function(){
            return {
                search_name: '',
                leaders    
            }
        },
        computed:{
           leaders_list() {
               if (this.search_name === '') return this.leaders 
               return _.filter(this.leaders, (leader) => leader.name.toLowerCase().indexOf(this.search_name.toLowerCase()) !== -1)
           }
        }
    }
</script>


